#scroll-container {
  .intro {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 100vh;
  }
  .filler {
    height: 100vh;
  }
  .card-container {
    display: flex;
    align-items: center;
    height: 100vh;
    margin: 0 5%;
    justify-content: flex-end;
    position: relative;
    .info {
      align-self: flex-end;
      padding: 4rem;
      outline-offset: -1rem;
      max-width: 500px;
      height: 350px;
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: center;
      gap: 2rem;
      width: 100%;
      z-index: 2;
      .bg {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        background-color: #0a3f23;
        z-index: -1;
        left: 0;
        clip-path: polygon(99% 75%, 100% 0%, 0% 0%, 0% 100%, 75% 100%);
      }
      .top-right,
      .bottom-left {
        position: absolute;
        width: 8rem;
        height: 4rem;
        z-index: 1;
        &::before {
          content: "";
          position: absolute;
          width: 0;
          height: 0;
        }
        &::after {
          content: "";
          position: absolute;
          width: 0;
          height: 0;
        }
      }
      .top-right {
        top: 1rem;
        right: 1rem;
        border-top: 0.7rem solid #25bc6a;
        border-right: 0.7rem solid #25bc6a;
        &::before {
          border-top: 0.7rem solid #25bc6a;
          border-left: 0.7rem solid transparent;
          left: -0.7rem;
          top: -0.7rem;
        }
        &::after {
          border-top: 0.7rem solid #25bc6a;
          border-left: 0.7rem solid transparent;
          right: -0.7rem;
          bottom: -0.7rem;
        }
      }
      .bottom-left {
        bottom: 1rem;
        left: 1rem;
        border-bottom: 0.7rem solid #25bc6a;
        border-left: 0.7rem solid #25bc6a;
        &::before {
          border-bottom: 0.7rem solid #25bc6a;
          border-right: 0.7rem solid transparent;
          left: -0.7rem;
          top: -0.7rem;
        }
        &::after {
          border-bottom: 0.7rem solid #25bc6a;
          border-right: 0.7rem solid transparent;
          right: -0.7rem;
          bottom: -0.7rem;
        }
      }
    }
  }
  .filler {
    height: 100vh;
  }
}

@media (min-width: 1600px) {
  #scroll-container .card-container .info {
    height: 400px;
  }
}
